import React, { useEffect, useState } from 'react';
import styled from 'styled-components';
import data from './data.json';

const PageContainer = styled.div`
  padding: 20px;
  font-family: CustomFont;
`;

const PageTitle = styled.p`
  font-size: 20px;
  margin-bottom: 10px;
  margin-top: 10px;
`;

const PageSubTitle = styled.p`
  font-size: 13px;
  margin-top: 5px;
  margin-bottom: 10px;
`;

const LevelContainer = styled.div`
  margin-top: 3%;
`;

const LevelHeader = styled.div`
  background: #ddd;
  padding: 10px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  border-radius: 10px;
  cursor: pointer;
`;

const LevelContent = styled.div`
  background: #f9f9f9;
  padding: 10px;
  display: ${props => (props.isOpen ? 'block' : 'none')};
  border-radius: 0px 0px 10px 10px;
`;

const GuideContainer = styled.div`
  background: #fff;
  padding: 10px;
  margin-bottom: 5px;
  border-radius: 10px;
  display: flex;
  flex-direction: column;
`;

const GuideItem = styled.div`
  display: flex;
  padding-top: 10px;
`;

const AttributeImages = styled.div`
  display: flex;
  align-items: center;
  margin-left: 8px;
`;

const AttributeImage = styled.img`
  width: 24px;
  height: 24px;
  margin-left: 5px;
`;

const VideoContainer = styled.div`
  margin-top: 10px;
`;

const HexagonalBeeInfo = () => {
  const [levels, setLevels] = useState([]);
  const [openLevels, setOpenLevels] = useState({});

  useEffect(() => {
    loadData();
  }, []); // 빈 배열을 넣어 첫 로드 시에만 실행되도록 수정

  const handleLevelClick = (levelId) => {
    setOpenLevels((prev) => ({
      ...prev,
      [levelId]: !prev[levelId],
    }));
  };

  const loadData = () => {
    setLevels(data.levels);
  }

  return (
    <PageContainer>
      <PageTitle>육각벌의 숲</PageTitle>
      <PageSubTitle>공략을 알고 싶은 단계를 선택하세요!</PageSubTitle>
      {levels.map(level => (
        <LevelContainer key={level.id}>
          <LevelHeader onClick={() => handleLevelClick(level.id)}>
            <span>{level.name}</span>
          </LevelHeader>
          <LevelContent isOpen={openLevels[level.id]}>
            <GuideContainer>
                <GuideItem>
                    <strong>전투력:</strong> {level['combat-power']}
                </GuideItem>
                <GuideItem>
                    <strong>추천 속성:</strong>
                    <AttributeImages>
                    {level['rec-attributes'].map(attribute => (
                        <AttributeImage
                        key={attribute}
                        src={`https://sseong-mi.github.io/Tower-of-Adventures/images/attributes/${attribute}.png`}
                        alt={attribute}
                        />
                    ))}
                    </AttributeImages>
                </GuideItem>
                <GuideItem>
                    <strong>추천 조합:</strong> {level['rec-combination'].join(', ')}
                </GuideItem>
                <GuideItem>
                    <strong>팁:</strong> {level.tip}
                </GuideItem>
                {level['rec-video'] && (
                  <VideoContainer>
                    <iframe
                      width="100%"
                      height="315"
                      src={`https://www.youtube.com/embed/${level['rec-video']}`}
                      title="YouTube video player"
                      frameBorder="0"
                      allow="accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture"
                      allowFullScreen
                    ></iframe>
                  </VideoContainer>
                )}
            </GuideContainer>
          </LevelContent>
        </LevelContainer>
      ))}
    </PageContainer>
  );
};

export default HexagonalBeeInfo;
